<template>
  <div class="content">
    <div class="banner">
        <banner />
    </div>

    <transition name="fade" appear>
        <div class="modal-overlay" v-if ="showModal" @click="showModal = false; value=''; msg = ''; showChangeRAMBasic = false; showChangeCPUBasic = false; showChangeBox = false; showChangeRAMDDR3 = false; showChangeRAMDDR4 = false; showChangeVGAMultimedia = false; showChangeCPUMultimedia = false;"> <br><br> </div>
    </transition>

    <div class="row">
        <div class="column">
            <button id="button-pc" title="Click here to choose your type of PC Build" @click=" showQuestionBasic = true; showQuestionMultimedia = false; showTabelMultimedia = false; value = ''; msg = ''; showKeyboard = false; showMonitor = false;">Everyday Use</button>
        </div>

        <div class="column">
            <button id="button-pc" title="Click here to choose your type of PC Build" @click=" showQuestionMultimedia = true; showQuestionBasic = false; showTabelBasic = false; value = ''; msg = ''; showKeyboard = false; showMonitor = false;">Mainstream / Online</button>
        </div>
    </div>
    
    <div class="change-box" v-if="showChangeBox">
        <table class="table table-striped table-borderes" v-if="showChangeRAMDDR4">
            <thead>
                <tr>
                    <th width="20%">Component Type</th>
                    <th width="40%">Component Name</th>
                    <th width="20%">Component Price</th>
                </tr>
            </thead>
            <tbody>
                <tr v-for="(product,index) in RAMDDR4" :key="product._id">
                    <td>{{product.product_type}}</td>
                    <td>{{product.product_name}}</td>
                    <td>Rp {{formatPrice(product.product_price)}}</td> 
                    <td><button id="button-change" title="Change Parts" @click=" changeRAMDDR4(index); ">Change Parts</button></td>
                </tr>

            </tbody>

        </table>

        <table class="table table-striped table-borderes" v-if="showChangeRAMDDR3">
            <thead>
                <tr>
                    <th width="20%">Component Type</th>
                    <th width="40%">Component Name</th>
                    <th width="20%">Component Price</th>
                </tr>
            </thead>
            <tbody>
                <tr v-for="(product,index) in RAMDDR3" :key="product._id">
                    <td>{{product.product_type}}</td>
                    <td>{{product.product_name}}</td>
                    <td>Rp {{formatPrice(product.product_price)}}</td> 
                    <td><button id="button-change" title="Change Parts" @click="changeRAMDDR3(index); ">Change Parts</button></td>
                </tr>

            </tbody>

        </table>

        <table class="table table-striped table-borderes" v-if="showChangeCPUBasic">
            <thead>
                <tr>
                    <th width="20%">Component Type</th>
                    <th width="40%">Component Name</th>
                    <th width="20%">Component Price</th>
                </tr>
            </thead>
            <tbody>
                <tr v-for="(product, index) in CPUMB" :key="product._id">
                    <td>CPU</td>
                    <td>{{product.component_cpu}}</td>
                    <td>Rp {{formatPrice(product.cpu_price)}}</td>
                    <td><button id="button-change" title="Change Parts" @click=" changeCPU(index); ">Change Parts</button></td>
                </tr>

            </tbody>

        </table>

        <table class="table table-striped table-borderes" v-if="showChangeCPUMultimedia">
            <thead>
                <tr>
                    <th width="20%">Component Type</th>
                    <th width="40%">Component Name</th>
                    <th width="20%">Component Price</th>
                </tr>
            </thead>
            <tbody>
                <tr v-for="(product, index) in CPUMBMULTIMEDIA" :key="product._id">
                    <td>CPU</td>
                    <td>{{product.component_cpu}}</td>
                    <td>Rp {{formatPrice(product.cpu_price)}}</td>
                    <td><button id="button-change" title="Change Parts" @click=" changeCPUMulti(index); ">Change Parts</button></td>
                </tr>

            </tbody>

        </table>

        <table class="table table-striped table-borderes" v-if="showChangeVGAMultimedia">
            <thead>
                <tr>
                    <th width="20%">Component Type</th>
                    <th width="40%">Component Name</th>
                    <th width="20%">Component Price</th>
                </tr>
            </thead>
            <tbody>
                <tr v-for="(product,index) in VGA" :key="product._id">
                    <td>{{product.product_type}}</td>
                    <td>{{product.product_name}}</td>
                    <td>Rp {{formatPrice(product.product_price)}}</td> 
                    <td><button id="button-change" title="Change Parts" @click=" changeVGA(index); ">Change Parts</button></td>
                </tr>

            </tbody>

        </table>

    </div>

    <div class="recommendation-box" v-if="showQuestionBasic">
        <h1>Everyday Use</h1>
        <h2>Specify your budget:</h2>
        <input type="range" value="0" min="2000000" max="3200000" step="50000" v-model.number="value" width="100%" class="input-range" @change="searchBuildBasic()">
        <input type="number" v-model.number="value" style="margin-left: 20px; width: 100px " @change="searchBuildBasic()"><br>
        <span>Rp {{formatPrice(value)}}</span><br><br>
        <div class="error-msg"> 
            {{msg}}
        </div>
        
        <h2 v-if="showTabelBasic">Our Recommendation:</h2>
        <table class="table table-striped table-borderes" v-if="showTabelBasic">
            <thead>
                <tr>
                    <th width="20%">Component Type</th>
                    <th width="40%">Component Name</th>
                    <th width="20%">Component Price</th>
                    <th width="20%"></th>
                </tr>
            </thead>
            <tbody>
                <tr>
                    <td>CPU</td>
                    <td>{{search_result.component_cpu}}</td>
                    <td>Rp {{formatPrice(search_result.cpu_price)}}</td>
                    <td><button id="button-change" title="Change Parts" @click=" changeCPUBasic = true; changeParts(); ">Change Parts</button></td>
                </tr>

                <tr>
                    <td>Motherboard</td>
                    <td>{{search_result.component_mb}}</td>
                    <td>Rp {{formatPrice(search_result.mb_price)}}</td>
                    <td></td>
                </tr>

                <tr>
                    <td>RAM</td>
                    <td>{{search_result.component_ram}}</td>
                    <td>Rp {{formatPrice(search_result.ram_price)}}</td>
                    <td><button id="button-change" title="Change Parts" @click=" changeRAMBasic = true; changeParts();">Change Parts</button></td>
                </tr>

                <tr>
                    <td>VGA</td>
                    <td>-</td>
                    <td>-</td>
                    <td></td>
                </tr>

                <tr>
                    <td>Storage</td>
                    <td>{{search_result.component_storage}}</td>
                    <td>Rp {{formatPrice(search_result.storage_price)}}</td>
                    <td></td>
                </tr>

                <tr>
                    <td>PSU</td>
                    <td>{{search_result.component_psu}}</td>
                    <td>Rp {{formatPrice(search_result.psu_price)}}</td>
                    <td></td>
                </tr>

                <tr>
                    <td>Case</td>
                    <td>{{search_result.component_case}}</td>
                    <td>Rp {{formatPrice(search_result.case_price)}}</td>
                    <td></td>
                </tr>

                <tr v-if="showMonitor">
                    <td>Monitor</td>
                    <td>-</td>
                    <td>-</td>
                    <td></td>
                </tr>

                <tr v-if="showKeyboard">
                    <td>Keyboard & Mouse</td>
                    <td>-</td>
                    <td>-</td>
                    <td></td>
                </tr>

                <tr v-if="search_result != null">
                    <td></td>
                    <td style="font-weight: bold">Total Price</td>
                    <td style="font-weight: bold">Rp {{formatPrice(search_result.total_price)}}</td>
                </tr>

            </tbody>

        </table>
        <button v-if="showTabelBasic" @click="showModal = true; showChangeBox = true; showMonitor = true" id="button-other" title="Add Monitor">Add Monitor</button>
        <button v-if="showTabelBasic" @click="showModal = true; showChangeBox = true; showKeyboard = true" id="button-other" title="Add Keyboard & Mouse">Add Keyboard & Mouse</button>
        <button v-if="showTabelBasic" @click="addAllToCartBasic()" id="button-add" title="Add all Products to cart">Add to Cart</button>
    </div>

    <div class="recommendation-box" v-if="showQuestionMultimedia">
        <h1>Mainstream / Online</h1>
        <h2>Specify your budget:</h2>
        <input type="range" value="0" min="0" max="10000000" step="100000" v-model.number="value" width="100%" class="input-range" @change="searchBuildMultimedia()">
        <input type="number" v-model.number="value" style="margin-left: 20px; width: 100px " @change="searchBuildMultimedia()"><br>
        <span>Rp {{formatPrice(value)}}</span><br><br>
        <div class="error-msg"> 
            {{msg}}
        </div>
        
        <h2 v-if="showTabelMultimedia">Our Recommendation:</h2>
        <table class="table table-striped table-borderes" v-if="showTabelMultimedia">
            <thead>
                <tr>
                    <th width="20%">Component Type</th>
                    <th width="40%">Component Name</th>
                    <th width="20%">Component Price</th>
                    <th width="20%"></th>
                </tr>
            </thead>
            <tbody>
                <tr>
                    <td>CPU</td>
                    <td>{{search_result.component_cpu}}</td>
                    <td>Rp {{formatPrice(search_result.cpu_price)}}</td>
                    <td><button id="button-change" title="Change Parts" @click="changeCPUMultimedia = true; changeParts()">Change Parts</button></td>
                </tr>

                <tr>
                    <td>Motherboard</td>
                    <td>{{search_result.component_mb}}</td>
                    <td>Rp {{formatPrice(search_result.mb_price)}}</td>
                </tr>

                <tr>
                    <td>RAM</td>
                    <td>{{search_result.component_ram}}</td>
                    <td>Rp {{formatPrice(search_result.ram_price)}}</td>
                    <td><button id="button-change" title="Change Parts" @click="changeRAMMultimedia = true; changeParts()">Change Parts</button></td>
                </tr>

                <tr>
                    <td>VGA</td>
                    <td>{{search_result.component_vga}}</td>
                    <td>Rp {{formatPrice(search_result.vga_price)}}</td>
                    <td><button id="button-change" title="Change Parts" @click="changeVGAMultimedia = true; changeParts()">Change Parts</button></td>
                </tr>

                <tr>
                    <td>Storage</td>
                    <td>{{search_result.component_storage}}</td>
                    <td>Rp {{formatPrice(search_result.storage_price)}}</td>
                    <td></td>
                </tr>

                <tr>
                    <td>PSU</td>
                    <td>{{search_result.component_psu}}</td>
                    <td>Rp {{formatPrice(search_result.psu_price)}}</td>
                    <td></td>
                </tr>

                <tr>
                    <td>Case</td>
                    <td>{{search_result.component_case}}</td>
                    <td>Rp {{formatPrice(search_result.case_price)}}</td>
                    <td></td>
                </tr>

                <tr v-if="showMonitor">
                    <td>Monitor</td>
                    <td>-</td>
                    <td>-</td>
                    <td></td>
                </tr>

                <tr v-if="showKeyboard">
                    <td>Keyboard & Mouse</td>
                    <td>-</td>
                    <td>-</td>
                    <td></td>
                </tr>


                <tr v-if="search_result != null">
                    <td></td>
                    <td style="font-weight: bold">Total Price</td>
                    <td style="font-weight: bold">Rp {{formatPrice(search_result.total_price)}}</td>
                </tr>
            </tbody>

        </table>
        <button v-if="showTabelMultimedia" @click="showModal = true; showChangeBox = true; showMonitor = true" id="button-other" title="Add Monitor">Add Monitor</button>
        <button v-if="showTabelMultimedia" @click="showModal = true; showChangeBox = true; showKeyboard = true" id="button-other" title="Add Keyboard & Mouse">Add Keyboard & Mouse</button>
        <button v-if="showTabelMultimedia" @click="addAllToCartMultimedia()" id="button-add" title="Add all Products to cart">Add to Cart</button>
    </div>
    
  </div>
</template>

<script>
import axios from "axios"
import BannerRecommendation from "../components/Recommendation/BannerRecommendation"

export default {
    name: "Recommendation",
    components: {
        "banner": BannerRecommendation
    },
    data() {
        return {
            changeCPUBasic: false,
            changeRAMBasic: false,
            changeCPUMultimedia: false,
            changeRAMMultimedia: false,
            changeVGAMultimedia: false,
            showChangeRAMDDR4: false,
            showChangeRAMDDR3: false,
            showChangeCPUBasic: false,
            showChangeCPUMultimedia: false,
            showChangeVGAMultimedia: false,
            showKeyboard: false,
            showMonitor: false,
            showChangeBox: false,
            showTabelBasic: false,
            showTabelMultimedia: false,
            showModal: false,
            showQuestionBasic: false,
            showQuestionMultimedia: false,
            indexRam: 0,
            indexCPUMB: 0,
            indexVGA: 0,
            changeRAM: [],
            msg : "",
            Cpu: [],
            Motherboard: [],
            RAM: [],
            RAMDDR4: [],
            RAMDDR3: [],
            RAMNULL: false,
            VGA: [],
            CPUMB: [],
            CPUMBMULTIMEDIA: [],
            Storage: [],
            PSU: [],
            Case: [],
            Default: [],
            Result: [
                {
                component_cpu: "",
                component_mb: "",
                component_ram: "",
                memory: "",
                cpu_price: 0,
                mb_price: 0,
                ram_price: 0,
                total_price: 0
                } 
            ] ,
            search: "",
            search_result: [],
            value: 0
        }
    },
    methods: {
        formatPrice(value) {
            let val = (value/1).toFixed(2).replace('.', ',')
            return val.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ".")
        },
        changeVGA(index) {
            this.search_result.total_price -= this.search_result.vga_price;

            this.search_result.component_vga = this.VGA[index].product_name;
            this.search_result.vga_price = this.VGA[index].product_price;

            this.search_result.total_price += this.VGA[index].product_price;
            this.indexVGA = index;
            this.showChangeVGAMultimedia = false;
            this.showChangeBox = false;
            this.showModal = false;

            this.value = this.search_result.total_price
        },
        changeCPU(index) {
            var current_price = this.search_result.cpu_price + this.search_result.mb_price;
            this.search_result.total_price -= current_price;

            this.search_result.component_cpu = this.CPUMB[index].component_cpu;
            this.search_result.cpu_price = this.CPUMB[index].cpu_price;

            this.search_result.component_mb = this.CPUMB[index].component_mb;
            this.search_result.mb_price = this.CPUMB[index].mb_price;

            if(this.search_result.memory != this.CPUMB[index].memory) {
                this.search_result.total_price -= this.search_result.ram_price;
                this.search_result.memory = this.CPUMB[index].memory;

                if(this.search_result.memory == "DDR3") {
                    this.search_result.component_ram = this.RAMDDR3[0].product_name;
                    this.search_result.ram_price = this.RAMDDR3[0].product_price;

                } else if(this.search_result.memory == "DDR4") {
                    this.search_result.component_ram = this.RAMDDR4[0].product_name;
                    this.search_result.ram_price = this.RAMDDR4[0].product_price;
                }
                
                this.RAMNULL = true;
                
            }

            this.indexCPUMB = index;
            
            this.search_result.total_price += this.CPUMB[index].total_price;
            this.showChangeCPUBasic = false;
            this.showChangeBox = false;
            this.showModal = false;

            this.value = this.search_result.total_price
        },
        changeCPUMulti(index) {
            var current_price = this.search_result.cpu_price + this.search_result.mb_price;
            this.search_result.total_price -= current_price;

            this.search_result.component_cpu = this.CPUMBMULTIMEDIA[index].component_cpu;
            this.search_result.cpu_price = this.CPUMBMULTIMEDIA[index].cpu_price;

            this.search_result.component_mb = this.CPUMBMULTIMEDIA[index].component_mb;
            this.search_result.mb_price = this.CPUMBMULTIMEDIA[index].mb_price;


            this.indexCPUMB = index;
            
            this.search_result.total_price += this.CPUMBMULTIMEDIA[index].total_price;
            this.showChangeCPUMultimedia = false;
            this.showChangeBox = false;
            this.showModal = false;

            this.value = this.search_result.total_price
        },
        changeRAMDDR3(index) {
            if(this.RAMNULL == true) {
                this.search_result.component_ram = this.RAMDDR3[index].product_name;
                this.search_result.ram_price = this.RAMDDR3[index].product_price;
                this.search_result.total_price += this.RAMDDR3[index].product_price;
                this.RAMNULL = false;

            } else {
                this.search_result.total_price -= this.search_result.ram_price;
                this.search_result.component_ram = this.RAMDDR3[index].product_name;
                this.search_result.ram_price = this.RAMDDR3[index].product_price;
                this.search_result.total_price += this.RAMDDR3[index].product_price;
            }

            this.value = this.search_result.total_price

            this.showChangeBox = false;
            this.showChangeRAMDDR3 = false;
            this.showModal = false;
        },
        changeRAMDDR4(index) {
            if(this.RAMNULL == true) {
                this.search_result.component_ram = this.RAMDDR4[index].product_name;
                this.search_result.ram_price = this.RAMDDR4[index].product_price;
                this.search_result.total_price += this.RAMDDR4[index].product_price;
                this.RAMNULL = false;

            } else {
                this.search_result.total_price -= this.search_result.ram_price;
                this.search_result.component_ram = this.RAMDDR4[index].product_name;
                this.search_result.ram_price = this.RAMDDR4[index].product_price;
                this.search_result.total_price += this.RAMDDR4[index].product_price;
            }

            this.value = this.search_result.total_price
            
            this.showChangeRAMDDR4 = false;
            this.showChangeBox = false;
            this.showModal = false;
        },
        changeParts(){
            if(this.showTabelBasic == true) {
                if(this.changeCPUBasic == true) {
                    this.showChangeCPUBasic = true;
                    this.showModal = true;
                    this.showChangeBox = true;

                } else if(this.changeRAMBasic == true) {
                    this.showChangeRAMBasic = true;
                    this.showModal = true;
                    this.showChangeBox = true;

                    if(this.search_result.memory == "DDR4") {
                        this.showChangeRAMDDR4 = true;

                    } else if(this.search_result.memory == "DDR3") {
                        this.showChangeRAMDDR3 = true;

                    }
                    
                }

            } else if(this.showTabelMultimedia == true) {
                if(this.changeCPUMultimedia == true) {
                    this.showChangeCPUMultimedia = true;
                    this.showModal = true;
                    this.showChangeBox = true;

                } else if(this.changeVGAMultimedia == true) {
                    this.showChangeVGAMultimedia = true;
                    this.showModal = true;
                    this.showChangeBox = true;

                } else if(this.changeRAMMultimedia == true) {
                    this.showChangeRAMDDR4 = true;
                    this.showModal = true;
                    this.showChangeBox = true;
                }
            }

            this.changeRAMBasic = false;
            this.changeCPUBasic = false;
            this.changeCPUMultimedia = false;
            this.changeRAMMultimedia = false;
            this.changeVGAMultimedia = false;
        },
        addAllToCartBasic() {
            for(var i = 0; i < this.RAM.length; i++) {
                if(this.search_result.component_ram == this.RAM[i].product_name) {
                    this.indexRam = i;
                }
            }

            if(window.confirm("Add all items to cart?")) {
                 //add CPU
                this.$store.dispatch('addProductToCart', {
                    product_name: this.Default[this.indexCPUMB].component_cpu,
                    product_price: this.Default[this.indexCPUMB].cpu_price,
                    quantity: 1,
                    product_image: this.Default[this.indexCPUMB].cpu_image
                });

                //add MB
                this.$store.dispatch('addProductToCart', {
                    product_name: this.Default[this.indexCPUMB].component_mb,
                    product_price: this.Default[this.indexCPUMB].mb_price,
                    quantity: 1,
                    product_image: this.Default[this.indexCPUMB].mb_image
                });

                //add ram
                this.$store.dispatch('addProductToCart', {
                    product_name: this.RAM[this.indexRam].product_name,
                    product_price: this.RAM[this.indexRam].product_price,
                    quantity: 1,
                    product_image: this.RAM[this.indexRam].path_image
                });

                //add storage
                this.$store.dispatch('addProductToCart', {
                    product_name: this.Default[0].component_storage,
                    product_price: this.Default[0].storage_price,
                    quantity: 1,
                    product_image: this.Default[0].storage_image
                });
                
                //add psu
                this.$store.dispatch('addProductToCart', {
                    product_name: this.Default[0].component_psu,
                    product_price: this.Default[0].psu_price,
                    quantity: 1,
                    product_image: this.Default[0].psu_image
                });

                //add case
                this.$store.dispatch('addProductToCart', {
                    product_name: this.Default[0].component_case,
                    product_price: this.Default[0].case_price,
                    quantity: 1,
                    product_image: this.Default[0].case_image
                });
                
                

                window.alert("Successfully added all item to cart");
                this.showTabelBasic = false;
                this.showModal = false;
                this.showQuestionBasic = false;
            }

           
        },
        addAllToCartMultimedia() {
            if(window.confirm("Add all items to cart?")) {
                 //add CPU
                this.$store.dispatch('addProductToCart', {
                    product_name: this.CPUMBMULTIMEDIA[this.indexCPUMB].component_cpu,
                    product_price: this.CPUMBMULTIMEDIA[this.indexCPUMB].cpu_price,
                    quantity: 1,
                    product_image: this.CPUMBMULTIMEDIA[this.indexCPUMB].cpu_image
                });

                //add MB
                this.$store.dispatch('addProductToCart', {
                    product_name: this.CPUMBMULTIMEDIA[this.indexCPUMB].component_mb,
                    product_price: this.CPUMBMULTIMEDIA[this.indexCPUMB].mb_price,
                    quantity: 1,
                    product_image: this.CPUMBMULTIMEDIA[this.indexCPUMB].mb_image
                });

                //add ram
                this.$store.dispatch('addProductToCart', {
                    product_name: this.RAMDDR4[this.indexRam].product_name,
                    product_price: this.RAMDDR4[this.indexRam].product_price,
                    quantity: 1,
                    product_image: this.RAMDDR4[this.indexRam].path_image
                });

                //add storage
                this.$store.dispatch('addProductToCart', {
                    product_name: this.Default[0].component_storage,
                    product_price: this.Default[0].storage_price,
                    quantity: 1,
                    product_image: this.Default[0].storage_image
                });

                //add vga
                this.$store.dispatch('addProductToCart', {
                    product_name: this.VGA[this.indexVGA].product_name,
                    product_price: this.VGA[this.indexVGA].product_price,
                    quantity: 1,
                    product_image: this.VGA[this.indexVGA].path_image
                });
                
                //add psu
                this.$store.dispatch('addProductToCart', {
                    product_name: this.Default[0].component_psu,
                    product_price: this.Default[0].psu_price,
                    quantity: 1,
                    product_image: this.Default[0].psu_image
                });

                //add case
                this.$store.dispatch('addProductToCart', {
                    product_name: this.Default[0].component_case,
                    product_price: this.Default[0].case_price,
                    quantity: 1,
                    product_image: this.Default[0].case_image
                });
                
                

                window.alert("Successfully added all item to cart");
                this.showTabelBasic = false;
                this.showModal = false;
                this.showQuestionBasic = false;
            }

           
        },
        searchBuildBasic() {
            this.showTabelBasic = false;
            this.msg = "";
            var temp = {};
            var list_choice = [];
            var index_cpumb = 0;
            var index_ram = 0;
            var x = 0;
            var highest_index_cpumb = 0;
            var highest_index_ram = 0;

            for(var z = 0; z < 14; z++) {
                list_choice[z] = {}
            }

            var fixed_cost = this.PSU[0].product_price + this.Storage[0].product_price + this.Case[0].product_price;
