    mounted() {
        axios.get('/api/products/cpu').then((response) => {
            // console.log(response.data);
            this.Cpu = response.data;
            const x = this.Cpu.length;

            for(var i = 0; i<x; i++) {
                this.Default[i] = {}
            }

            for(var j = 0; j<this.Cpu.length; j++) {
                this.Default[j].cpu_image = this.Cpu[j].path_image
                this.Default[j].component_cpu = this.Cpu[j].product_name
                this.Default[j].cpu_price = this.Cpu[j].product_price
                this.Default[j].total_price = this.Cpu[j].product_price
                this.Default[j].pc_type = this.Cpu[j].pc_type;
            }
            
        })
        .catch((error) => {
            console.log(error);
        })

        axios.get('/api/products/motherboard').then((response) => {
            // console.log(response.data);
            this.Motherboard = response.data;
            var count_basic = 0;
            var count_multimedia = 0;
            var index_basic = 0;
            var index_multimedia = 0;
            

            for(var i = 0; i < this.Cpu.length; i++) {
                for(var j = 0; j < this.Motherboard.length; j++) {
                    if(this.Cpu[i].chipset == this.Motherboard[j].chipset) {
                        this.Default[i].mb_image = this.Motherboard[j].path_image
                        this.Default[i].component_mb = this.Motherboard[j].product_name
                        this.Default[i].mb_price = this.Motherboard[j].product_price
                        this.Default[i].memory = this.Motherboard[j].memory
                        this.Default[i].total_price += this.Motherboard[j].product_price
                    }
                }
                
            }

            for(var z = 0; z < this.Default.length; z++) {
                if(this.Default[z].pc_type == "basic") { 
                    count_basic++;

                } else if(this.Default[z].pc_type == "multimedia") {
                    count_multimedia++;
                }
            }

            console.log(count_multimedia);
            console.log(count_basic)

            for(var y = 0; y < count_basic; y++) {
                this.CPUMB[y] = {}
            }

            for(var a = 0; a < count_multimedia; a++) {
                this.CPUMBMULTIMEDIA[a] = {}
            }
            
            for(var x = 0; x < this.Default.length; x++) {
                if(this.Default[x].pc_type == "basic") {
                    this.CPUMB[index_basic].component_cpu = this.Default[x].component_cpu;
                    this.CPUMB[index_basic].cpu_price = this.Default[x].cpu_price;
                    this.CPUMB[index_basic].component_mb = this.Default[x].component_mb;
                    this.CPUMB[index_basic].mb_price = this.Default[x].mb_price;
                    this.CPUMB[index_basic].memory = this.Default[x].memory;
                    this.CPUMB[index_basic].total_price = this.Default[x].cpu_price + this.Default[x].mb_price;

                    index_basic++;

                } else if(this.Default[x].pc_type == "multimedia") {
                    this.CPUMBMULTIMEDIA[index_multimedia].component_cpu = this.Default[x].component_cpu;
                    this.CPUMBMULTIMEDIA[index_multimedia].cpu_price = this.Default[x].cpu_price;
                     this.CPUMBMULTIMEDIA[index_multimedia].cpu_image = this.Default[x].cpu_image;
                    this.CPUMBMULTIMEDIA[index_multimedia].mb_image = this.Default[x].mb_image;
                    this.CPUMBMULTIMEDIA[index_multimedia].component_mb = this.Default[x].component_mb;
                    this.CPUMBMULTIMEDIA[index_multimedia].mb_price = this.Default[x].mb_price;
                    this.CPUMBMULTIMEDIA[index_multimedia].memory = this.Default[x].memory;
                    this.CPUMBMULTIMEDIA[index_multimedia].total_price = this.Default[x].cpu_price + this.Default[x].mb_price;

                    index_multimedia++;
                }
                
                
                
            }
             

        })
        .catch((error) => {
            console.log(error);
        })

        axios.get('/api/products/ram').then((response) => {
            // console.log(response.data);
            this.RAM = response.data;
            var count_ddr3 = 0;
            var count_ddr4 = 0;

            for(var a = 0; a <3; a++) {
                this.RAMDDR3[a] = {}
            }

            for(var b = 0; b < 2; b++) {
                this.RAMDDR4[b] = {}
            }

            for(var i = 0; i < this.RAM.length; i++) {
                if(this.RAM[i].memory == "DDR3") {
                    this.RAMDDR3[count_ddr3] = this.RAM[i];
                    count_ddr3++;

                } else if(this.RAM[i].memory == "DDR4") {
                    this.RAMDDR4[count_ddr4] = this.RAM[i];
                    count_ddr4++;
                }
            }

        })
        .catch((error) => {
            console.log(error);
        })

        axios.get('/api/products/storage').then((response) => {
            // console.log(response.data);
            this.Storage = response.data;

            for(var i = 0; i<this.Default.length; i++) {
                for(var j = 0; j<this.Storage.length; j++) {
                    this.Default[i].storage_image = this.Storage[j].path_image
                    this.Default[i].component_storage = this.Storage[j].product_name;
                    this.Default[i].storage_price = this.Storage[j].product_price;
                    this.Default[i].total_price += this.Storage[j].product_price;
                    
                }
            }

        })
        .catch((error) => {
            console.log(error);
        })

        axios.get('/api/products/psu').then((response) => {
            // console.log(response.data);
            this.PSU = response.data;

            for(var i = 0; i<this.Default.length; i++) {
                for(var j = 0; j<this.PSU.length; j++) {
                    this.Default[i].psu_image = this.PSU[j].path_image
                    this.Default[i].component_psu = this.PSU[j].product_name;
                    this.Default[i].psu_price = this.PSU[j].product_price;
                    this.Default[i].total_price += this.PSU[j].product_price;
                    
                }
            }

        })
        .catch((error) => {
            console.log(error);
        })

        axios.get('/api/products/case').then((response) => {
            // console.log(response.data);
            this.Case = response.data;

            for(var i = 0; i<this.Default.length; i++) {
                for(var j = 0; j<this.Case.length; j++) {
                    this.Default[i].case_image = this.Case[j].path_image
                    this.Default[i].component_case = this.Case[j].product_name;
                    this.Default[i].case_price = this.Case[j].product_price;
                    this.Default[i].total_price += this.Case[j].product_price;
                    
                }
            }

        })
        .catch((error) => {
            console.log(error);
        })

        //get all vga
        axios.get('/api/products/vga').then((response) => {
            // console.log(response.data);
            this.VGA = response.data;
        })
        .catch((error) => {
            console.log(error);
        })

    }
}
</script>

