import axios from 'axios';
import router from '../router'

const state = {
    token: localStorage.getItem('token'),
    user: {},
    status: ''.status,
    error: null,
    cart: []
};

const getters = {
    isLoggedIn: state => !!state.token,
    authState: state => state.status,
    user: state => state.user,
    error: state => state.error,
    cart: state => state.cart,
    cartItemCount: (state) => {
        let total_item = 0;
        
        state.cart.forEach(item => {
            total_item += item.quantity;
            
        })

        return total_item;
    },
    cartTotalPrice: (state) => {
        let total_price = 0;
        
        state.cart.forEach(item => {
            total_price += item.product_price * item.quantity;
            
        })

        return total_price;
    },
    
};

const actions = {
    //login user
    async login({ commit }, user) {
        commit('auth_request');
        try {
            // let res = await axios.post('http://localhost:5000/api/users/login', user)
            let res = await axios.post('/api/users/login', user)
            if(res.data.success) {
                const token = res.data.token;
                const user = res.data.user;

                //store the token to localstorage
                localStorage.setItem('token', token);
                //set axios default
                axios.defaults.headers.common['Authorization'] = token;
                commit('auth_success', token, user);
        }
        return res;
        } catch (err) {
            commit('auth_error', err);
        }
    },

    async loginAdmin({ commit }, user) {
        commit('auth_request');
        try {
            // let res = await axios.post('http://localhost:5000/api/users/login_admin', user)
            let res = await axios.post('/api/users/login_admin', user)
            if(res.data.success) {
                const token = res.data.token;
                const user = res.data.user;
    
                //store the token to localstorage
                localStorage.setItem('token', token);
                //set axios default
                axios.defaults.headers.common['Authorization'] = token;
                commit('auth_success', token, user);
        }
            return res;
        } catch (err) {
            commit('auth_error', err);
        }
        
        
    },


    //get useris profile
    async getProfile({commit}) 
    {
        commit('profile_request');
        // let res = await axios.get('http://localhost:5000/api/users/profile');
        let res = await axios.get('/api/users/profile');
        commit('user_profile', res.data.user);
        return res;
    },

    //Register user
    async register({ commit }, userData) {
        try {
            commit('register_request');
            // let res = await axios.post('http://localhost:5000/api/users/register', userData);
            let res = await axios.post('/api/users/register', userData);
            if(res.data.success !== undefined) {
                commit('register_success');
            }
            return res;
             
        } catch (err) {
            commit('register_error', err);
        }
    },

    

    //Logout user 
    async logout({ commit }) {
        await localStorage.removeItem('token');
        commit('logout');
        delete axios.defaults.headers.common['Authorization'];
        router.push('/login');
        return
    },

    //add item to cart
    async addProductToCart({commit}, { product_name, product_price, quantity, product_image}) {
        commit('add_to_cart', {product_name, product_price, quantity, product_image});
    },

    //remove item from cart
    async removeProductFromCart({commit}, toRemove) {
        commit('remove_from_cart', toRemove)
    },

    //clear cart
    async clearCart({commit}) {
        commit('clear_cart')
    },

    async editSaveProfile({commit}, user) {
        // let res = await axios.put('http://localhost:5000/api/users/profile/edit_profile', user);
        let res = await axios.put('/api/users/profile/edit_profile', user);
        let userData = res.data.data.attributes;
        commit('edit_profile', userData)
        return userData
    },

    async clearError({commit}) {
        commit('clear_error')
    }

};

const mutations = {
    auth_request(state) {
        state.error = null
        state.status = 'loading'
    }, 
    auth_success(state, token, user) {
        state.token =  token
        state.user = user
        state.status = 'success'
        state.error = null
    },
    auth_error(state, err) {
        state.error = err.response.data.msg 
    },
    register_request(state) {
        state.error = null
        state.status = 'loading'
    },
    register_success(state) {
        state.error = null
        state.status = 'success'
    },
    register_error(state, err) {
        state.error = err.response.data.msg
    },
    logout(state) {
        state.error = null
        state.status = ''
        state.token = ''
        state.user = ''
    },
    profile_request(state){
        state.status = 'loading'
    },
    user_profile(state, user) {
        state.user = user
    },
    add_to_cart(state, {product_name, product_price, quantity, product_image}) {
        let productInCart = state.cart.find(item => {
            return item.product_name === product_name
        });

        if(productInCart) {
            productInCart.quantity += quantity
            return
        }

        state.cart.push({product_name, product_price, quantity, product_image})
    },
    remove_from_cart(state, toRemove) {
        state.cart = state.cart.filter(item => {
            return item.product_name !== toRemove.product_name
        })
    },
    clear_cart(state) {
        state.cart = []
    },
    clear_error(state) {
        state.error = null
    },
    edit_profile(state, user) {
        state.user.forEach(item => {
            if(item._id == user._id) {
                item.user;
            }
        })
    }
};

export default {
    state,
    actions,
    mutations,
    getters
}