<script>
import axios from "axios";
import { mapActions } from "vuex";
import {required, email, minLength, sameAs} from "vuelidate/lib/validators";

export default {
  name: 'AdminPanel',
  data(){
    return{
      Product_type: [],
      Products: [],
      CPU: [],
      Motherboard: [],
      Ram: [],
      Storage: [],
      PSU: [],
      Case: [],
      perPage: 2,
      currentPage: 1,
      Users: [],
      select: {
        product_type: "",
      },
      showModal: false,
      showUpdate: false,
      showAdd: false,
      product_name: '',
      product_type: '',
      product_price: '',
      url_product: '',
      add_product_name: '',
      add_product_type: '',
      add_product_price: '',
      add_url_product: 'test_url',
      name: '',
      username: '',
      email: '',
      address: '',
      password: '',
      confirm_password: '',
      birthday: '',
      add_name: '',
      add_user_name: '',
      add_email: '',
      add_address: '',
      add_password: '',
      add_confirm_password: '',
      add_birthday: '',
      is_admin: false,
      Selected: [],
    }
  },
  validations: {
    add_user_name: {required},
    add_name: {required},
    add_email: {required, email},
    add_password: {required, minLength: minLength(6)},
    add_confirm_password: {required, sameAsPassword: sameAs('add_password')},
    add_address: {required},
    add_birthday: {required},
    username: {required},
    name: {required},
    email: {required, email},
    password: {required, minLength: minLength(6)},
    confirm_password: {required, sameAsPassword: sameAs('password')},
    address: {required},
    add_product_price: {required},
    add_product_name: {required},
    add_product_type: {required},
    product_price: {required},
    product_type: {required},
    product_name: {required}
  },
  components: {},
  computed: {
    userRows(){
      return this.Users.length;
    },
    productRows(){
      return this.Products.length;
    },
    cpuRows(){
      return this.CPU.length;
    },
    mbRows(){
      return this.Motherboard.length;
    },
    ramRows(){
      return this.Ram.length;
    }
  },
  methods: {
    ...mapActions(['register']),
    validate: function() {
      this.$v.$touch();
      if (this.$v.$invalid) {
        return;
      }
    },
    deleteUser(users, id){
      let apiURL = `/api/users/delete/${id}`;
      let index = this.Users.findIndex(i => i._id === id);
      console.log(id)
      console.log(users)
      console.log(apiURL);
      console.log(index)
      if(window.confirm("Delete the selected user?")){
        axios.delete(apiURL, id)
      .then(()=>{
        this.Users.splice(index, 1);
        window.alert("Successfully deleted user");
        location.reload();
      })
      .catch(error => {
        console.log(error);
        })
      }

      
    },
    deleteProduct(product, id){
      let apiURL = `/api/products/delete/${id}`;
      let index = this.Products.findIndex(i => i._id === id);
      console.log(id)
      console.log(product)
      if(window.confirm("Delete the selected product?")){
        axios.delete(apiURL, id)
      .then(()=>{
        this.Products.splice(index, 1);
        window.alert("Successfully deleted product");
        location.reload();
      }).catch(error => {
        console.log(error);
        })
      }

      
    },
    editUser(users, id) {
      let apiURL = `/api/products/edit/${id}`;

      // let date = this.$options.filter.date_format(users.birthday);

      this.name = users.name,
      this.username = users.username,
      this.email  = users.email,
      this.address = users.address,
      this.birthday = users.birthday,
      this.is_admin = users.isAdmin

      console.log(apiURL);
      this.Selected = users;

    },
    updateUser() {
      console.log(this.Selected)
      var correct = 0;

      let newData = {
        _id: this.Selected._id,
        name: this.name,
        username: this.username,
        email: this.email,
        address: this.address,
        // birthday: this.birthday,
        isAdmin: this.is_admin,
        password: this.password
      }

      if(this.username != "") {
        correct++;
      }

      if(this.name != "") {
        correct++;
      }

      if(this.password != "" && this.password.length >= 6) {
        correct++;
      }

      if(this.confirm_password != "" && this.confirm_password == this.password) {
        correct++;
      }

      if(this.address != "") {
        correct++;
      }

      if(this.email != "") {
        correct++;
      }

      if(this.birthday!= "") {
        correct++;
      }

      console.log(correct)

      if(correct == 7) {
        console.log(newData)
        axios.post('/api/users/profile/edit_profile', newData).then((response) => {
          console.log(response);
          window.alert("Successfully edit user");
          location.reload();
        }).catch((error) => {
          console.log(error); 
        })
      }
      

      
    },
    addUser() {
      var admin = false;
      var correct = 0;

      if(this.is_admin == "true") {
        admin = true
      } 

      if(this.add_user_name != "") {
        correct++;
      }

      if(this.add_name != "") {
        correct++;
      }

      if(this.add_password != "" && this.add_password.length >= 6) {
        correct++;
      }

      if(this.add_confirm_password != "" && this.add_confirm_password == this.add_password) {
        correct++;
      }

      if(this.add_address != "") {
        correct++;
      }

      if(this.add_email != "") {
        correct++;
      }

      if(this.add_birthday!= "") {
        correct++;
      }

      let UserData = {
          username: this.add_user_name,
          name: this.add_name,
          password: this.add_password,
          confirm_password: this.add_confirm_password,
          address: this.add_address,
          email: this.add_email,
          birthday: this.add_birthday,
          isAdmin : admin
      }

      if(correct == 7) {
        this.register(UserData)
      .then(res => {
        if(res.data.success) {
          console.log("success")
          window.alert("Successfully add user");
          location.reload();

        } 
      }).catch(err => {
          console.log(err);
      })
      }

      

      console.log(UserData)

      
      
    },
    formatPrice(value) {
      let val = (value/1).toFixed(2).replace('.', ',')
      return val.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ".")
    },
    editProduct(product, id) {
      let apiURL = `/api/products/edit/${id}`;
      this.product_name = product.product_name,
      this.product_type = product.product_type,
      this.product_price  = product.product_price,
      this.url_product  = product.url_product

      console.log(apiURL);
      this.Selected = product;
    },
    updateProduct() {
      var correct = 0;
      

      if(this.product_name != '') {
        correct++;
      }

      if(this.product_price != '') {
        correct++;
      }

      if(this.Product_type != '') {
        correct++;
      }


      console.log(correct)

      this.Selected.product_name = this.product_name;
      this.Selected.product_type = this.product_type;
      this.Selected.product_price = this.product_price;
      this.Selected.url_product = this.url_product;
      
      if(correct == 3) {
        axios.post('/api/products/edit/product_update', this.Selected).then((response) => {
        console.log(response);
        window.alert("Successfully update product");
        location.reload();
      }).catch((error) => {
        console.log(error); 
      })
      }
      

      

    },
    saveProduct(){
      var correct = 0;

      if(this.add_product_type != '') {
        correct++;
      }

      if(this.add_product_price != '') {
        correct++;
      }

      if(this.add_product_name != '') {
        correct++;
      }
      
      let newData = {
        product_name: this.add_product_name,
        product_price: this.add_product_price,
        product_type: this.add_product_type,
        url_product: this.add_url_product,
      }

      if(correct == 3) {
        axios.post('/api/products/add/product_add', newData).then((response => {
          console.log(response);
          window.alert("Successfully add product");
          location.reload();
        })).catch((error) => {
          console.log(error)
        })

      }

    },
  },
  mounted(){
  //get all products
    this.Product_type[0] = "cpu"
    this.Product_type[1] = "motherboard"
    this.Product_type[2] = "ram"
    this.Product_type[3] = "vga"
    this.Product_type[4] = "storage"
    this.Product_type[5] = "psu"
    this.Product_type[6] = "case"

  axios.get('/api/products').then((response) => { 
    console.log(response.data);
    this.Products = response.data;
    



  }).catch((error) => {
    console.log(error);
  })

  //get all cpu
  axios.get('/api/products/cpu').then((response) => {
    console.log(response.data);
    this.CPU = response.data;

  }).catch((error) => {
    console.log(error);
  })

  

  axios.get('/api/products/motherboard').then((response) => {
    console.log(response.data);
    this.Motherboard = response.data;

  }).catch((error) => {
    console.log(error);
  })

  axios.get('/api/products/ram').then((response) => {
    console.log(response.data);
    this.Ram = response.data;

  }).catch((error) => {
    console.log(error);
  })

  axios.get('/api/products/storage').then((response) => {
    console.log(response.data);
    this.Storage = response.data;
  })
  .catch((error) => {
    console.log(error);
  })

  axios.get('/api/products/psu').then((response) => {
    console.log(response.data);
    this.PSU = response.data;
  })
    .catch((error) => {
     console.log(error);
  })

  axios.get('/api/products/case').then((response) => {
    console.log(response.data);
    this.Case = response.data;
  })
  .catch((error) => {
    console.log(error);
  })
  
  axios.get('/api/users').then((response) => {
    console.log(response.data);
    this.Users = response.data;

  }).catch((error) => {
    console.log(error);
  })



  }
}

</script>

