<template>
  <div>
        <transition name="fade" appear>
            <div class="modal-overlay" v-if="showModal" @click="showModal = false"> <br><br> </div>
        </transition>

         <div class="product-detail-cart" v-if="showModal">
            <h1>Your Item</h1>

            <ul>
                <li class="cart=row" v-if="!cart.length">Your cart is currently empty</li>
                <li class="cart-row" v-for="(item,index) in cart" :key="item._id">
                    <b></b>
                    <img :src="require('@/assets/products/' + item.product_image)" id="img-cart">
                    <div class="cart-body">
                        <b>{{item.product_name}}</b><br>
                        <b>Price: Rp {{formatPrice(item.product_price)}}</b><br>
                        <b>Quantity: {{item.quantity}}</b>
                    </div>
                    <button @click.prevent="remove_product(index)" id="button-remove">Remove</button>
                </li>
            </ul>
            <a v-if="cart.length" href="" @click.prevent="clear_cart()" style="float: right">Clear Cart</a>
            <b v-if="cart.length">Total Item: {{cartItemCount}} </b> <br><br><br>
            <h2 v-if="cart.length">Total Price: Rp {{formatPrice(cartTotalPrice)}} </h2> <br><br><br>
            <button @click="showModal = false" id="button-close-cart">Close</button>
            <button v-if="cart.length" @click="checkLogin(user)" id="button-checkout">Checkout</button>
            
        </div>

      <button id="button-cart" @click="showModal = true">
          <i class="fas fa-shopping-cart"></i>
      </button>
  </div>
</template>

<script>
import {mapGetters, mapActions} from "vuex";
import axios from "axios";
import store from "../store/index";

export default {
    name: 'Cart',
    methods: {
        ...mapActions(['getProfile']),
        remove_product(index) {
            this.toRemove = this.cart[index]
            this.$store.dispatch('removeProductFromCart', this.toRemove)
        },
        clear_cart() {
            this.$store.dispatch('clearCart');
        },
        formatPrice(value) {
            let val = (value/1).toFixed(2).replace('.', ',')
            return val.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ".")
        },
        checkLogin(user) {
            if(!store.getters.isLoggedIn) {
                window.alert("You are not logged in, please login first");
                this.$router.push("login");
                this.showModal = false;
            } else {
                if(window.confirm("Checkout your order now?")) {
                    this.checkout(user)
                }
            }
        },

        checkout(user) {
            for(var x = 0; x < this.cart.length; x++) {
                this.CheckoutItem.transaction_detail[x] = {}
            }
            
            this.CheckoutItem.customer_id = user._id;
            this.CheckoutItem.total_price = this.cartTotalPrice;

            for(var i = 0; i < this.cart.length; i++) {
                this.CheckoutItem.transaction_detail[i].product_image = this.cart[i].product_image;
                this.CheckoutItem.transaction_detail[i].product_name = this.cart[i].product_name;
                this.CheckoutItem.transaction_detail[i].product_price = this.cart[i].product_price;
                this.CheckoutItem.transaction_detail[i].quantity = this.cart[i].quantity;
            }
            
            axios.post('/api/transactions/add', this.CheckoutItem).then((response) => {
                console.log(response);
            }).catch((error) => {
                console.log(error); 
            })

            this.clear_cart();
            this.showModal = false;
            this.$router.push("profile");
                
        }
            

        
    },
    data() {
        return {
            showModal: false,
            toRemove: [],
            CheckoutItem : 
            {
                customer_id: "",
                total_price: 0,
                transaction_detail: [ 
                {
                    product_image: "",
                    product_name: "",
                    product_price: 0,
                    quantity: 0
                 }
                        
                ]
             }
            
        }
    },
    computed: {
        ...mapGetters(["cart","cartItemCount","cartTotalPrice","user"]),
    }
}
</script>

<style scoped>
#button-cart {
    outline: none;
    font-size: 25px;
    text-align: center;
    width: 100px;
    height: 50px;
    top: 10%;
    right: 1%;
    position: fixed;
	color: #17a2b8;
	background-color: #343a40;
    border-radius: 20px;
	border: solid 1px;
	border-color: #17a2b8;
    font-weight: bold;
    margin-right: 5px;
}

#button-cart:focus {
    outline: none;
}

button i {
    margin-top: 10px;
}

.cart-row {
    padding: 5px;
    border: 2px solid #17a2b8;
    border-radius: 20px;
    margin-bottom: 10px;
}

#button-remove {
    border-radius: 20px;
    outline: none;
    float: right;
    margin-left: 20px;
    width: 100px;
	color: #17a2b8;
    height: 50px;
	background-color: #343a40;
	border: solid 2px;
    font-weight: bold;
	border-color: #17a2b8;
    margin-right: 10px;
}

#button-remove:hover{
	color: #fff;
	background-color: #17a2b8;
}

#button-cart:hover{
	color: #fff;
	background-color: #17a2b8;
}

#button-close-cart:hover{
	color: #fff;
	background-color: #17a2b8;
}

.product-detail-cart{
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    z-index: 99;
    width: 100%;
    max-width: 600px;
    background-color: white;
    border-radius: 20px;
    padding: 20px; 
    height: 500px;
    border: 1px dashed gray;
}

#button-close-cart {
    outline: none;
    border-radius: 20px;
    width: 130px;
	color: #17a2b8;
    height: 50px;
	background-color: #343a40;
	border: solid 2px;
    font-weight: bold;
	border-color: #17a2b8;
    margin-right: 10px;
}

#button-checkout {
    outline: none;
    border-radius: 20px;
    float: right;
    width: 130px;
	color: #17a2b8;
    height: 50px;
	background-color: #343a40;
	border: solid 2px;
    font-weight: bold;
	border-color: #17a2b8;
    margin-right: 10px;
}

#button-checkout:hover{
	color: #fff;
	background-color: #17a2b8;
}

.product-detail-cart h1 {
    margin-bottom: 10px;
}

.product-detail-cart h2 {
    font-weight: bold;
    float: right;
}

#img-cart {
    float: left;
    width: 100px;
    height: 100px;
    border: 1px solid #17a2b8;
    border-radius: 20px;
}

.cart-body b{
    font-size: 20px;
    margin-left: 10px;
}

.modal-overlay {
    position: fixed;
    height: 100%;
    top: 0;
    bottom: 0;
    right: 0;
    left: 0;
    z-index: 98;
    background-color: rgba(0, 0, 0, 0.3);
}
</style>