<template>
    <div>
        <br><br><br>
        
        <div class="profile-outer">
            <h1 id="profile-title">Profile</h1>
            <div class="profile-box" v-if="user">
                <div id="profile-image">
                    <img :src="require('@/assets/users/user_default.png')" id="img-profile">

                    <!-- <form @submit.prevent="sendFile" enctype="multipart/form-data">
                        <div id="image-upload">
                            
                            <button>Change Profile Picture</button>
                            {{message}}
                        </div>
                    </form> -->

                    <!-- <input type="file" id="file" />
                    <label for="file" >Change Profile Picture</label> -->
                    
                    
                </div>
                
                <div id="profile-data">
                    <h4>Name: {{user.name}}</h4>
                    <h4>Username: {{user.username}}</h4>
                    <h4>Email: {{user.email}}</h4>
                    <h4>Birthday: {{user.birthday | date_format}}</h4>
                    
                </div>

                <button @click="editProfile(user); showModal = true; showEdit=true" id="button-edit">Edit Profile</button>
                <button @click="showTransaction(user); showModal = true; showOrder = true" id="button-transaction">View All Order</button>
                
            </div>
        </div>
        
        <!-- MODAL -->
        <transition name="fade" appear>
            <div class="modal-overlay" v-if="showModal" @click="showModal = false; showEdit= false; showOrder = false"> <br><br> </div>
        </transition>

        <!-- Transactions Detail -->
        <div class="order-profile-box" v-if="showOrder">
            <h1>All Order</h1>
            {{msg}}

            <table class="table table-striped" v-for="(transaction,index) in Transactions" :key="transaction._id">
                <tr class="header">
                    <th width="40%">Order: {{index + 1}}</th>
                    <th width="30%"></th>
                    <th width="30%">Total Price: Rp {{formatPrice(transaction.total_price)}}</th>
                </tr>
            
                <tr v-for="item in transaction.transaction_detail" :key="item.product_name">
                    <td>Product Name: {{item.product_name}}</td>
                    <td>Product Price: Rp {{formatPrice(item.product_price)}}</td>
                    <td>Quantity: {{item.quantity}}</td>
                </tr>
               <br>
            </table>
            

        </div>

        <!-- EDIT PROFILE FORM -->
        <div class="edit-profile-box" v-if="showEdit">
            <h1>Edit Your Profile</h1>

            <form @submit.prevent="saveProfile(user)" style="background:none">
                <div class="textbox">
                    <i class="fas fa-user"></i>
                    <input type="text"  placeholder="Name..." v-model="name" id="name" name="name" :class="{ 'is-invalid': $v.name.$error }" />
                    <div v-if="!$v.name.required" class="invalid-feedback">You have to input your Name</div>
                </div>

                <div class="textbox">
                    <i class="fas fa-user"></i>
                    <input type="text"  placeholder="Username..." v-model="username" id="username" name="username" :class="{ 'is-invalid': $v.username.$error }" />
                    <div v-if="!$v.username.required" class="invalid-feedback">You have to input your Username</div>
                </div>

                <div class="textbox">
                    <i class="fas fa-envelope"></i>
                    <input type="email" placeholder="Email" v-model="email" id="email" name="email" :class="{ 'is-invalid': $v.email.$error }" />
                        <div v-if="$v.email.$error" class="invalid-feedback">
                            <span v-if="!$v.email.required">You have to input your Email</span>
                            <span v-if="!$v.email.email">Email is invalid</span>
                        </div>
                </div>

                <div class="textbox">
                    <i class="fas fa-home"></i>
                    <textarea id="address" placeholder="Address" cols="10" rows="4" name="address" v-model="address" :class="{ 'is-invalid': $v.address.$error }"></textarea>
                    <div v-if="!$v.address.required" class="invalid-feedback">You have to input your Address</div>
                </div>

                <div class="textbox">
                    <i class="fas fa-lock"></i>
                    <input type="password" placeholder="Password" v-model="password" id="password" name="password" :class="{ 'is-invalid': $v.password.$error }" />
                    <div v-if="$v.password.$error" class="invalid-feedback">
                        <span v-if="!$v.password.required">You have to input your Password</span>
                        <span v-if="!$v.password.minLength">Password must be at least 6 characters</span>
                    </div>
                </div>

                <div class="textbox">
                    <i class="fas fa-lock"></i>
                    <input id="confirm_password" type="password" placeholder="Confirm Password" name="confirm_password" v-model="confirm_password" :class="{ 'is-invalid': $v.confirm_password.$error }">
                    <div v-if="$v.confirm_password.$error" class="invalid-feedback">
                        <span v-if="!$v.confirm_password.required">Confirm Password must be the same as Password</span>
                        <span v-else-if="!$v.confirm_password.sameAsPassword">Passwords must match</span>
                    </div>
                </div>
                
                <input type="submit" class="btn-save" value="Save" v-on:click="validate">&nbsp;&nbsp;&nbsp;&nbsp;
            </form>

        </div>
    </div>
</template>

<script>
import { mapActions, mapGetters} from "vuex";
import {required, email, minLength, sameAs} from "vuelidate/lib/validators";
import axios from "axios";

export default {
    data () {
        return {
            temp_user: this.user,
            Transactions: [],
            file: "",
            showModal: false,
            showOrder: false,
            showEdit: false,
            name: "",
            username: "",
            email: "",
            birthday: "",
            address: "",
            password: "",
            confirm_password: "",
            msg : "",
            message: ""
        }
    },
    validations: {
            username: {required},
            name: {required},
            email: {required, email},
            password: {required, minLength: minLength(6)},
            confirm_password: {required, sameAsPassword: sameAs('password')},
            address: {required},
            birthday: {required}
    },
    computed: mapGetters(['user']),
    methods: {
        ...mapActions(['getProfile']),
        formatPrice(value) {
            let val = (value/1).toFixed(2).replace('.', ',')
            return val.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ".")
        },
        showTransaction(user) {
            axios.get('/api/transactions/by_user', {
                params: {
                    user_id : user._id
                }
            }).then((response) => {
            console.log(response.data);
            this.Transactions = response.data;

            if(this.Transactions.length == 0) {
                this.msg = "No Order"
            }

            })
            .catch((error) => {
                console.log(error);
                
            })
        },
        selectFile() {
            const file = this.$refs.file.files[0];
            this.file = file;
            
        },
        async sendFile() {
            const formData = new FormData();
            formData.append('file', this.file);

            try {
                await axios.post('http://localhost:5000/upload', formData);
                this.message = "success upload"
                this.file = ""
            } catch (error) {
                console.log(error);
                this.message = "failed upload"
            }
            
        },
        validate: function() {
            this.$v.$touch();
            if (this.$v.$invalid) {
                return;
            }
        },
        saveProfile(user) {
            // console.log(user)
            let newData = {
                _id: user._id,
                name: this.name,
                username: this.username,
                email: this.email,
                address: this.address,
                birthday: this.birthday,
                password: this.password
            }

            console.log(newData)
            // this.$store.dispatch('editSaveProfile', user)
            axios.post('/api/users/profile/edit_profile', newData).then((response) => {
                console.log(response);
            }).catch((error) => {
                console.log(error); 
            })
            
            this.showModal = false;
            this.showEdit = false;
            location.reload();
            
        },
        editProfile(user) {
            this.birthday = user.birthday
            this.name = user.name
            this.username = user.username
            this.email = user.email
            this.address = user.address

            console.log(user._id)
            console.log(user)
         }
    },
    created() {
        this.getProfile();
    },
    name: "Profile"
    
}
</script>

<style scoped>
.profile-outer {
    margin-right: 400px;
    margin-left: 400px;
}

.profile-box {
    border: 1px dashed gray;
    padding: 20px;
    border-radius: 20px;
}

#profile-title {
    font-weight: bold;
}

.edit-profile-box{
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    z-index: 99;
    width: 100%;
    max-width: 550px;
    background-color: white;
    border-radius: 20px;
    padding: 20px; 
    height: 560px;
    border: 1px dashed gray;
}

.order-profile-box{
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    z-index: 99;
    width: 100%;
    max-width: 1000px;
    background-color: white;
    border-radius: 20px;
    padding: 20px; 
    height: 600px;
    border: 1px dashed gray;
}

.textbox {
    width: 100%;
    overflow: hidden;
    font-size: 20px;
    padding: 5px 0;
    margin: 8px 0;
    border-bottom: 1px solid #007c97;
}

.textbox i {
    margin-top: 5px;
    width: 30px;
    float: left;
    text-align: center;
    color: #007c97;
}

.textbox input, textarea{
    border: none;
    outline: none;
    background: none;
    color: #007c97;
    font-size: 20px;
    width: 90%;
    float: left;
    margin: 0 5px;
}

#img-profile {
    width: 200px;
    height: 200px;
    border: 1px solid black;
    border-radius: 50%;
    margin-bottom: 10px;
}

#profile-image {
    float: left;
    width: 25%;
}

input[type=submit] {
    margin-top: 20px;
    float: right;
    width: 100px;
	color: #17a2b8;
    height: 50px;
	background-color: #343a40;
	border: solid 2px;
    font-weight: bold;
	border-color: #17a2b8;
}

input[type=submit]:hover{
	color: #fff;
	background-color: #17a2b8;
}

#button-edit {
    outline: none;
    border-radius: 20px;
    width: 100px;
	color: #17a2b8;
    height: 50px;
	background-color: #343a40;
	border: solid 2px;
    font-weight: bold;
	border-color: #17a2b8;
}

#button-edit:hover{
	color: #fff;
	background-color: #17a2b8;
}

#button-transaction {
    outline: none;
    border-radius: 20px;
    margin-left: 20px;
    width: 150px;
	color: #17a2b8;
    height: 50px;
	background-color: #343a40;
	border: solid 2px;
    font-weight: bold;
	border-color: #17a2b8;
}

#button-transaction:hover{
	color: #fff;
	background-color: #17a2b8;
}

#profile-image label{
    margin-top: 10px;
    font-weight: bold;
}

#profile-image input{
    margin-top: -200px;
    font-weight: bold;
}

#image-upload {
    display: inline;
}

#profile-data { 
    width: 75%;
}

#profile-data h4{ 
    font-weight: bold;
}

.header{
    background-color: #17a2b8;
    border-radius: 20px;
}

td {
    border-bottom: 1px solid #343a40;
}


.modal-overlay {
    position: fixed;
    height: 100%;
    top: 0;
    bottom: 0;
    right: 0;
    left: 0;
    z-index: 98;
    background-color: rgba(0, 0, 0, 0.3);
}

[type="file"] {
    border: 0;
    clip: rect(0, 0, 0, 0);
    height: 1px;
    overflow: hidden;
    padding: 0;
    position: absolute !important;
    white-space: nowrap;
    width: 1px;
}
 
[type="file"] + label {
    font-size: 15px;
    overflow: hidden;
    width: 150px;
	color: #17a2b8;
    height: 50px;
	background-color: #343a40;
	border: solid 2px;
    font-weight: bold;
	border-color: #17a2b8;
}
  
[type="file"] + label:hover {
    color: #fff;
	background-color: #17a2b8;
}
  

.fade-enter-active, .fade-leave-active {
    transition: opacity 0.5s;
}

.fade-enter, .fade-leave-to {
    opacity: 0;
}

@media screen and (min-width: 1300px) {
    #profile-box {
        padding: 10px;
        margin-right: 200px;
        margin-left: 200px;
    }

}
</style>