<template>
  <div id="register-form">
    <div class="register-box">
      <h1>Register to EasyPCAssemble</h1>
      <Errors v-if="error" :msg="error"/>

      <form @submit.prevent="registerUser" style="background:none">
        <div class="textbox">
          <i class="fas fa-user"></i>
          <input type="text"  placeholder="Name..." v-model="name" id="name" name="name" :class="{ 'is-invalid': $v.name.$error }" autocomplete="off"/>
          <div v-if="!$v.name.required" class="invalid-feedback">You have to input your Name</div>
        </div>

        <div class="textbox">
          <i class="fas fa-user"></i>
          <input type="text"  placeholder="Username..." v-model="username" id="username" name="username" :class="{ 'is-invalid': $v.username.$error }" autocomplete="off"/>
          <div v-if="!$v.username.required" class="invalid-feedback">You have to input your Username</div>
        </div>

        <div class="textbox">
          <i class="fas fa-envelope"></i>
          <input type="email" placeholder="Email" v-model="email" id="email" name="email" :class="{ 'is-invalid': $v.email.$error }" autocomplete="off"/>
              <div v-if="$v.email.$error" class="invalid-feedback">
                <span v-if="!$v.email.required">You have to input your Email</span>
                <span v-if="!$v.email.email">Email is invalid</span>
              </div>
        </div>

        <div class="textbox">
          <i class="fas fa-calendar-alt"></i>
          <input id="birthday" type="date" placeholder="Birthday" name="birthday" v-model="birthday" :class="{ 'is-invalid': $v.birthday.$error }" autocomplete="off"/>
          <div v-if="!$v.birthday.required" class="invalid-feedback">You have to input your Birthday</div>
        </div>

        <div class="textbox">
          <i class="fas fa-home"></i>
          <textarea id="address" placeholder="Address" cols="10" rows="4" name="address" v-model="address" :class="{ 'is-invalid': $v.address.$error }" autocomplete="off"></textarea>
          <div v-if="!$v.address.required" class="invalid-feedback">You have to input your Address</div>
        </div>

        <div class="textbox">
          <i class="fas fa-lock"></i>
          <input type="password" placeholder="Password" v-model="password" id="password" name="password" :class="{ 'is-invalid': $v.password.$error }" />
          <div v-if="$v.password.$error" class="invalid-feedback">
            <span v-if="!$v.password.required">You have to input your Password</span>
            <span v-if="!$v.password.minLength">Password must be at least 6 characters</span>
          </div>
        </div>

        <div class="textbox">
          <i class="fas fa-lock"></i>
          <input id="confirm_password" type="password" placeholder="Confirm Password" name="confirm_password" v-model="confirm_password" :class="{ 'is-invalid': $v.confirm_password.$error }">
          <div v-if="$v.confirm_password.$error" class="invalid-feedback">
            <span v-if="!$v.confirm_password.required">Confirm Password must be the same as Password</span>
            <span v-else-if="!$v.confirm_password.sameAsPassword">Passwords must match</span>
          </div>
        </div>
        
         <input type="submit" class="btn" value="Register" v-on:click="validate">&nbsp;&nbsp;&nbsp;&nbsp;
         <router-link to="/login" class="card-link" style="color:white; margin-top:20px">Already have an account?</router-link>


      </form>

    </div>
  </div>


  
</template>

<script>
import { mapActions, mapGetters } from "vuex";
import Errors from '@/components/Errors.vue';
import {required, email, minLength, sameAs} from "vuelidate/lib/validators";

export default {
  data () {
    return {
      name: "",
      username: "",
      email: "",
      birthday: "",
      address: "",
      password: "",
      confirm_password: "",
      msg : []
    };
  },
  validations: {
    username: {required},
    name: {required},
    email: {required, email},
    password: {required, minLength: minLength(6)},
    confirm_password: {required, sameAsPassword: sameAs('password')},
    address: {required},
    birthday: {required}
  },
  methods: {
    ...mapActions(['register']),
    validate: function() {
      this.$v.$touch();
      if (this.$v.$invalid) {
        return;
      }
    },
    registerUser() {
      var correct = 0;

      if(this.username != "") {
        correct++;
      }

      if(this.name != "") {
        correct++;
      }

      if(this.password != "" && this.password.length >= 6) {
        correct++;
      }

      if(this.confirm_password != "" && this.confirm_password == this.password) {
        correct++;
      }

      if(this.address != "") {
        correct++;
      }

      if(this.email != "") {
        correct++;
      }

      if(this.birthday!= "") {
        correct++;
      }

      let user = {
        name: this.name,
        username: this.username,
        email: this.email,
        birthday: this.birthday,
        address: this.address,
        password: this.password,
        confirm_password: this.confirm_password,

      }

      if(correct == 7) {
        this.register(user)
      .then(res => {
        if(res.data.success) {
          this.$router.push("/login");
        } 
      }).catch(err => {
          console.log(err);
      })
      }
      
    }
  },
  name : "RegisterForm",
  components: {
    Errors
  },
  computed: {
    ...mapGetters(["error"])
  },
  created() {
    this.$store.dispatch('clearError');
  }
}
</script>

<style scoped>

.register-box {
  font-family: sans-serif;
  width: 400px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%,-50%);
  color: white;
}

.register-box h1 {
  font-weight: bold;
  font-size: 40px;
  border-bottom: 6px solid #007c97;
  padding: 15px 0;
  margin-bottom: 50px;
  color: white;
}

.textbox {
  width: 100%;
  overflow: hidden;
  font-size: 20px;
  padding: 5px 0;
  margin: 8px 0;
  border-bottom: 1px solid #007c97;
}

.textbox i {
  margin-top: 5px;
  width: 30px;
  float: left;
  text-align: center;
  color: #007c97;
}

.textbox input, textarea{
  border: none;
  outline: none;
  background: none;
  color: white;
  font-size: 20px;
  width: 90%;
  float: left;
  margin: 0 5px;
}

.btn {
  margin-bottom: 20px;
  margin-top: 10px;
  font-weight: bold;
  width: 100%;
  background: none;
  border: 2px solid #007c97;
  color: #007c97;
}

.btn:hover {
	color: #fff;
	background-color: #007c97;
}

::placeholder {
  color: #007c97;
}

input[type=date] {
  color: #007c97;
}

.error {
  overflow: hidden;
  height: 50px;
  font-size: 20px;
}

</style>