<template>
  <div id="login-form">
    <div class="login-box">
      <h1>Welcome to EasyPCAssemble</h1>
      <Errors v-if="error" :msg="error"/>

      <form @submit.prevent="loginUser" style="background:none">
        <div class="textbox">
          <i class="fas fa-user"></i>
          <input type="text"  placeholder="Username..." v-model="username" id="username" name="username" :class="{ 'is-invalid': submitted && $v.username.$error }" autocomplete="off"/>
          <div v-if=" submitted && !$v.username.required" class="invalid-feedback">You have to input your username</div>
        </div>

        <div class="textbox">
          <i class="fas fa-lock"></i>
          <input type="password"  placeholder="Password..." v-model="password" id="password" name="password" :class="{ 'is-invalid': $v.password.$error }" />
          <div v-if="!$v.password.required" class="invalid-feedback">You have to input your password</div>
        </div>

        <input type="submit" class="btn" value="Login" v-on:click="validate">&nbsp;&nbsp;&nbsp;&nbsp;
        <router-link to="/register" class="card-link" style="color:white; margin-top:20px">Click here to register</router-link>
      </form>
    
    </div>
  </div>


  
</template>

<script>
import {mapActions, mapGetters} from 'vuex';
import {required} from "vuelidate/lib/validators";
import Errors from "../Errors";

export default {
  data () {
    return {
      username: "",
      password: "",
      success: "",
      submitted: false
    };
  },
  validations: {
    username: {required},
    password: {required}
  },
  methods: {
    ...mapActions(['login']),
    validate: function() {
      this.$v.$touch();
      if (this.$v.$invalid) {
        return;
      }
    },
    loginUser() {
      this.submitted = true
      var correct = 0;

      if(this.username != "") {
        correct++;
      }

      if(this.password != "") {
        correct++;
      }

      if(correct == 2 ) {
        let user = {
        username: this.username,
        password: this.password
        };
        this.login(user)
        .then(res => {
          if(res.data.success) {
            this.$router.push('/profile');
            
          }
        }).catch(err => {
            console.log(err);
          
        })
      }
      

      }
  },
  name : "LoginForm",
  components: {
    Errors
  },
  computed: {
    ...mapGetters(["error"])
  },
  created() {
    this.$store.dispatch('clearError');
  }
}
</script>

<style scoped>

.login-box {
  font-family: sans-serif;
  width: 400px;
  position: absolute;
  top: 40%;
  left: 50%;
  transform: translate(-50%,-50%);
  color: white;
}

.login-box h1 {
  font-weight: bold;
  font-size: 40px;
  border-bottom: 6px solid #007c97;
  padding: 15px 0;
  margin-bottom: 50px;
  color: white;
}

.textbox {
  width: 100%;
  overflow: hidden;
  font-size: 20px;
  padding: 5px 0;
  margin: 8px 0;
  border-bottom: 1px solid #007c97;
}

.textbox i {
  margin-top: 5px;
  width: 30px;
  float: left;
  text-align: center;
  color: #007c97;
}

.textbox input {
  border: none;
  outline: none;
  background: none;
  color: white;
  font-size: 20px;
  width: 80%;
  float: left;
  margin: 0 5px;
}

.btn {
  font-weight: bold;
  margin-top: 10px;
  width: 100%;
  background: none;
  border: 2px solid #007c97;
  color: #007c97;
}

.btn:hover {
	color: #fff;
	background-color: #007c97;
}


.error {
  overflow: hidden;
  height: 50px;
  font-size: 20px;
}

::placeholder {
  color: #007c97;
}

</style>