                                        <h1 id="product-name" @click="showCPU(index); showModal = true">{{product.product_name}}</h1>
                                        <small id="product-type">Product Type: {{product.product_type | capitalize}}</small>
                                    </div>

                                    <div class="col-right" style="width:30%">
                                        <h1 id="product-price">Rp {{formatPrice(product.product_price)}}</h1>
                                        <button id="button-view" title="View this Product's detail" @click="showCPU(index); showModal = true" >View Product</button>
                                        <button id="button-add" title="Add this Product to cart" @click="addToCartCPU(index)">Add to Cart</button>
                                    </div>
                                    
                                    
                                </div>
                            </li>
                        </ul>
                        </div>

                    </b-tab> 

                    <b-tab title="Motherboard" title-item-class="mytab">
                        <div class="column-product">
                            <ul>
                            <li v-for="(product,index) in Motherboard" :key="product._id" class="product-row">
                                <div class="body">
                                    <img :src="require('@/assets/products/' + product.path_image)" id="img-product">
                                     <div class="col-center" style="width:60%">
                                        <h1 href="" id="product-name" @click="showMB(index); showModal = true">{{product.product_name}}</h1>
                                        <small id="product-type">Product Type: {{product.product_type | capitalize}}</small>
                                    </div>

                                    <div class="col-right" style="width:30%">
                                        <h1 id="product-price">Rp {{formatPrice(product.product_price)}}</h1>
                                        <button id="button-view" title="View this Product's detail" @click="showMB(index); showModal = true">View Product</button>
                                         <button id="button-add" title="Add this Product to cart" @click="addToCartMB(index)">Add to Cart</button>
                                    </div>
                                    
                                    
                                </div>
                            </li>
                        </ul>
                        </div>
                    </b-tab> 
                    <b-tab title="RAM" title-item-class="mytab">
                        <div class="column-product">
                            <ul>
                            <li v-for="(product,index) in RAM" :key="product._id" class="product-row">
                                <div class="body">
                                    <img :src="require('@/assets/products/' + product.path_image)" id="img-product">
                                     <div class="col-center" style="width:60%">
                                        <h1 href="" id="product-name" @click="showRAM(index); showModal = true">{{product.product_name}}</h1>
                                        <small id="product-type">Product Type: {{product.product_type | capitalize}}</small>
                                    </div>

                                    <div class="col-right" style="width:30%">
                                        <h1 id="product-price">Rp {{formatPrice(product.product_price)}}</h1>
                                        <button id="button-view" title="View this Product's detail" @click="showRAM(index); showModal = true">View Product</button>
                                        <button id="button-add" title="Add this Product to cart" @click="addToCartRAM(index)">Add to Cart</button>
                                    </div>
                                    
                                    
                                </div>
                            </li>
                        </ul>
                        </div>
                    </b-tab>
                    <b-tab title="VGA" title-item-class="mytab">
                        <div class="column-product">
                            <ul>
                                <li v-for="(product,index) in VGA" :key="product._id" class="product-row">
                                    <div class="body">
                                        <img :src="require('@/assets/products/' + product.path_image)" id="img-product">
                                        <div class="col-center" style="width:60%">
                                            <h1 href="" id="product-name" @click="showVGA(index); showModal = true">{{product.product_name}}</h1>
                                            <small id="product-type">Product Type: {{product.product_type | capitalize}}</small>
                                        </div>

                                        <div class="col-right" style="width:30%">
                                            <h1 id="product-price">Rp {{formatPrice(product.product_price)}}</h1>
                                            <button id="button-view" title="View this Product's detail" @click="showVGA(index); showModal = true">View Product</button>
                                            <button id="button-add" title="Add this Product to cart" @click="addToCartVGA(index)">Add to Cart</button>
                                        </div>
                                        
                                        
                                    </div>
                                </li>
                            </ul>
                        </div>
                    </b-tab>
                    <b-tab title="Storage" title-item-class="mytab" >
                        <div class="column-product">
                            <ul>
                                <li v-for="(product,index) in Storage" :key="product._id" class="product-row">
                                    <div class="body">
                                        <img :src="require('@/assets/products/' + product.path_image)" id="img-product">
                                        <div class="col-center" style="width:60%">
                                            <h1 href="" id="product-name" @click="showStorage(index); showModal = true">{{product.product_name}}</h1>
                                            <small id="product-type">Product Type: {{product.product_type | capitalize}}</small>
                                        </div>

                                        <div class="col-right" style="width:30%">
                                            <h1 id="product-price">Rp {{formatPrice(product.product_price)}}</h1>
                                            <button id="button-view" title="View this Product's detail" @click="showStorage(index); showModal = true">View Product</button>
                                            <button id="button-add" title="Add this Product to cart" @click="addToCartStorage(index)">Add to Cart</button>
                                        </div>
                                        
                                        
                                    </div>
                                </li>
                            </ul>
                        </div>
                    </b-tab>
                    <b-tab title="Power Supply" title-item-class="mytab">
                        <div class="column-product">
                            <ul>
                                <li v-for="(product,index) in PSU" :key="product._id" class="product-row">
                                    <div class="body">
                                        <img :src="require('@/assets/products/' + product.path_image)" id="img-product">
                                        <div class="col-center" style="width:60%">
                                            <h1 href="" id="product-name" @click="showPSU(index); showModal = true">{{product.product_name}}</h1>
                                            <small id="product-type">Product Type: {{product.product_type | capitalize}}</small>
                                        </div>

                                        <div class="col-right" style="width:30%">
                                            <h1 id="product-price">Rp {{formatPrice(product.product_price)}}</h1>
                                            <button id="button-view" title="View this Product's detail" @click="showPSU(index); showModal = true">View Product</button>
                                            <button id="button-add" title="Add this Product to cart" @click="addToCartPSU(index)">Add to Cart</button>
                                        </div>
                                        
                                        
                                    </div>
                                </li>
                            </ul>
                        </div>
                    </b-tab>
                    <b-tab title="Case" title-item-class="mytab">
                        <div class="column-product">
                            <ul>
                                <li v-for="(product,index) in Case" :key="product._id" class="product-row">
                                    <div class="body">
                                        <img :src="require('@/assets/products/' + product.path_image)" id="img-product">
                                        <div class="col-center" style="width:60%">
                                            <h1 href="" id="product-name" @click="showCASE(index); showModal = true">{{product.product_name}}</h1>
                                            <small id="product-type">Product Type: {{product.product_type | capitalize}}</small>
                                        </div>

                                        <div class="col-right" style="width:30%">
                                            <h1 id="product-price">Rp {{formatPrice(product.product_price)}}</h1>
                                            <button id="button-view" title="View this Product's detail" @click="showCASE(index); showModal = true">View Product</button>
                                            <button id="button-add" title="Add this Product to cart" @click="addToCartCASE(index)">Add to Cart</button>
                                        </div>
                                        
                                        
                                    </div>
                                </li>
                            </ul>
                        </div>
                    </b-tab>
                </b-tabs>
            </div>
        </div>

    </div>
</template>

<script>
import axios from 'axios'

export default {
    filters: {
        capitalize: function (value) {
            if (!value) return ''
            value = value.toString()
            return value.toUpperCase()
        }
    },
    name: 'AllProduct',
    data() {
        return {
            perPage: 2,
            currentPage: 1,
            Products: [],
            CPU: [],
            Motherboard: [],
            RAM: [],
            Storage: [],
            PSU: [],
            Case: [],
            VGA: [],
            showModal: false,
            Selected: [],
            url_product: "",
            quantity: 1
        }
    },
    methods: {
        showCPU(index) {
            this.Selected = this.CPU[index]
            this.Selected.index = index
            this.url_product = this.CPU[index].url_product
        },
        showMB(index) {
            this.Selected = this.Motherboard[index]
            this.Selected.index = index
            this.url_product = this.Motherboard[index].url_product
        },
        showRAM(index) {
            this.Selected = this.RAM[index]
            this.Selected.index = index
            this.url_product = this.RAM[index].url_product
        },
        showStorage(index) {
            this.Selected = this.Storage[index]
            this.Selected.index = index
            this.url_product = this.Storage[index].url_product
        },
        showPSU(index) {
            this.Selected = this.PSU[index]
            this.Selected.index = index
            this.url_product = this.PSU[index].url_product
        },
        showCASE(index) {
            this.Selected = this.Case[index]
            this.Selected.index = index
            this.url_product = this.Case[index].url_product
        },
        showVGA(index) {
            this.Selected = this.VGA[index]
            this.Selected.index = index
            this.url_product = this.VGA[index].url_product
        },
        addToCart(index) {
            if(this.Selected.product_type == "cpu") {
                this.Selected = this.CPU[index]

            } else if(this.Selected.product_type == "motherboard") {
                this.Selected = this.Motherboard[index]

            } else if(this.Selected.product_type == "ram") {
                this.Selected = this.RAM[index]
            }

            if(this.quantity == 0) {
                this.quantity = 1

            } else {
                this.$store.dispatch('addProductToCart', {
                    product_name: this.Selected.product_name,
                    product_price: this.Selected.product_price,
                    quantity: this.quantity,
                    product_image: this.Selected.path_image
                })
            }

            window.alert("Successfully added item to cart");
        },
        addToCartCPU(index) {
            this.Selected = this.CPU[index]
            if(this.quantity == 0) {
                this.quantity = 1

            } else {
                this.$store.dispatch('addProductToCart', {
                    product_name: this.Selected.product_name,
                    product_price: this.Selected.product_price,
                    quantity: this.quantity,
                    product_image: this.Selected.path_image
                })
            }

            window.alert("Successfully added item to cart");
        },
        addToCartMB(index) {
            this.Selected = this.Motherboard[index]
            if(this.quantity == 0) {
                return
                
            } else {
                this.$store.dispatch('addProductToCart', {
                    product_name: this.Selected.product_name,
                    product_price: this.Selected.product_price,
                    quantity: this.quantity,
                    product_image: this.Selected.path_image
                })
            }

            window.alert("Successfully added item to cart");
        },
        addToCartRAM(index) {
            this.Selected = this.RAM[index]
            if(this.quantity == 0) {
                return
                
            } else {
                this.$store.dispatch('addProductToCart', {
                    product_name: this.Selected.product_name,
                    product_price: this.Selected.product_price,
                    quantity: this.quantity,
                    product_image: this.Selected.path_image
                })
            }

            window.alert("Successfully added item to cart");
        },
        addToCartVGA(index) {
            this.Selected = this.VGA[index]
            if(this.quantity == 0) {
                return
                
            } else {
                this.$store.dispatch('addProductToCart', {
                    product_name: this.Selected.product_name,
                    product_price: this.Selected.product_price,
                    quantity: this.quantity,
                    product_image: this.Selected.path_image
                })
            }

            window.alert("Successfully added item to cart");
        },
        addToCartStorage(index) {
            this.Selected = this.Storage[index]
            if(this.quantity == 0) {
                this.quantity = 1;

            } else {
                this.$store.dispatch('addProductToCart', {
                    product_name: this.Selected.product_name,
                    product_price: this.Selected.product_price,
                    quantity: this.quantity,
                    product_image: this.Selected.path_image
                })
            }

            window.alert("Successfully added item to cart");
        },
        addToCartPSU(index) {
            this.Selected = this.PSU[index]
            if(this.quantity == 0) {
                this.quantity = 1;

            } else {
                this.$store.dispatch('addProductToCart', {
                    product_name: this.Selected.product_name,
                    product_price: this.Selected.product_price,
                    quantity: this.quantity,
                    product_image: this.Selected.path_image
                })
            }

            window.alert("Successfully added item to cart");
        },
        addToCartCASE(index) {
            this.Selected = this.Case[index]
            if(this.quantity == 0) {
                this.quantity = 1;

            } else {
                this.$store.dispatch('addProductToCart', {
                    product_name: this.Selected.product_name,
                    product_price: this.Selected.product_price,
                    quantity: this.quantity,
                    product_image: this.Selected.path_image
                })
            }

            window.alert("Successfully added item to cart");
        },
        formatPrice(value) {
            let val = (value/1).toFixed(2).replace('.', ',')
            return val.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ".")
        }
    },
    computed: {
        rows() {
            return this.CPU.length;
        }
    
        
    },
    components: {},
    mounted() {
        //get all product
        axios.get('/api/products').then((response) => {
            console.log(response.data);
            this.Products = response.data;
            
        })
        .catch((error) => {
            console.log(error);
        });

        //get all cpu
        axios.get('/api/products/cpu').then((response) => {
            console.log(response.data);
            this.CPU = response.data;
        })
        .catch((error) => {
            console.log(error);
        })

        //get all motherboard
        axios.get('/api/products/motherboard').then((response) => {
            console.log(response.data);
            this.Motherboard = response.data;
        })
        .catch((error) => {
            console.log(error);
        })

        //get all ram
        axios.get('/api/products/ram').then((response) => {
            console.log(response.data);
            this.RAM = response.data;
        })
        .catch((error) => {
            console.log(error);
        })

        //get all hdd
        axios.get('/api/products/storage').then((response) => {
            console.log(response.data);
            this.Storage = response.data;
        })
        .catch((error) => {
            console.log(error);
        })

        //get all psu
        axios.get('/api/products/psu').then((response) => {
            console.log(response.data);
            this.PSU = response.data;
        })
        .catch((error) => {
            console.log(error);
        })

        //get all case
        axios.get('/api/products/case').then((response) => {
            console.log(response.data);
            this.Case = response.data;
        })
        .catch((error) => {
            console.log(error);
        })

        //get all vga
        axios.get('/api/products/vga').then((response) => {
            console.log(response.data);
            this.VGA = response.data;
        })
        .catch((error) => {
            console.log(error);
        })
    }
}

</script>

<style>
