}

#button-view:hover{
	color: #fff;
	background-color: #17a2b8;
}

/* MODAL STYLE */

.modal-overlay {
    position: fixed;
    height: 100%;
    top: 0;
    bottom: 0;
    right: 0;
    left: 0;
    z-index: 98;
    background-color: rgba(0, 0, 0, 0.3);
}

.product-detail { 
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    z-index: 99;
    width: 100%;
    max-width: 1000px;
    background-color: white;
    border-radius: 20px;
    padding: 20px; 
    height: 500px;
    border: 1px dashed gray;
}

#img-product-detail {
    float: left;
    width: 350px;
    height: 350px;
    border: 1px solid #17a2b8;
    border-radius: 15px;
}

.product-detail-body b, .product-detail-body a {
    margin-bottom: 10px;
    font-size: 20px;
    margin-left: 20px;
}

.product-detail-body input {
    font-size: 15px;
    margin-left: 10px;
    width: 50px;
}

.product-detail-body {
    padding: 20px;
    border: 2px solid #17a2b8;
    width: 100%;
    height: 400px;
    border-radius: 20px;
}

#button-close {
    outline: none;
    border-radius: 20px;
    margin-left: 20px;
    width: 130px;
	color: #17a2b8;
    height: 50px;
	background-color: #343a40;
	border: solid 2px;
    font-weight: bold;
	border-color: #17a2b8;
