}


.product-detail-body button {
    margin-top: 100px;
}

.fade-enter-active, .fade-leave-active {
    transition: opacity 0.5s;
}

.fade-enter, .fade-leave-to {
    opacity: 0;
}

@media screen and (min-width: 1300px) {
    .product-table {
        padding: 10px;
